___CSS_LOADER_EXPORT___.push([module.id, `*, *::before, *::after {
    box-sizing: border-box;
}

* {
    margin: 0;
}
  
body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
}
  
input, button, textarea, select {
    font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
}
  
#root, #__next {
    isolation: isolate;
}

html {
  --var-color-primary: #354034;
  --var-color-secondary: #708C7C;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #faf8f2;
  width: calc(100dvw - (100dvw - 100%));
}

.wave {
  --mask: 
    radial-gradient(15px at 50% calc(100% - 20px), #000 99%, #0000 101%) calc(50% - 20px) 0/40px 100%, 
    radial-gradient(15px at 50% calc(100% + 10px), #0000 99%, #000 101%) 50% calc(100% - 8px)/40px 100% repeat-x;
  -webkit-mask: var(--mask);
  mask: var(--mask);
  background-color: var(--var-color-secondary);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 15px;
}

.wave p {
  color: #f6f6f9;
  font-size: minmax(.90rem, 1rem);
  font-style: italic;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between; 
  background-color: transparent;
  width: 100%;
  padding: 20px 25px;
}

header img {
  height: 50px;
  width: 50px;
  border-radius: 5px;
}

.header-buttons {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header-buttons button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--var-color-primary);
  width: 120px;
  height: 50px;
  color: #f6f6f9;
  border: none;
  border-radius: 4px;
  transition: all 0.150s ease-in-out;
  font-size: 1rem;
}

header button:hover {
  background-color: #302b2b;
  scale: 1.1;
}

#content {
  width: 100%;
  height: calc(100vh - 100px);
}

/* content for homepage */

.homepage {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: transparent;
  width: 100%;
  height: 100%;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,gBAAgB;IAChB,mCAAmC;AACvC;;AAEA;IACI,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;AACtB;;AAEA;EACE,4BAA4B;EAC5B,8BAA8B;AAChC;;AAEA;EACE,kCAAkC;EAClC,yBAAyB;EACzB,qCAAqC;AACvC;;AAEA;EACE;;gHAE8G;EAC9G,yBAAyB;EACzB,iBAAiB;EACjB,4CAA4C;EAC5C,YAAY;EACZ,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,oBAAoB;AACtB;;AAEA;EACE,cAAc;EACd,+BAA+B;EAC/B,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,6BAA6B;EAC7B,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,0CAA0C;EAC1C,YAAY;EACZ,YAAY;EACZ,cAAc;EACd,YAAY;EACZ,kBAAkB;EAClB,kCAAkC;EAClC,eAAe;AACjB;;AAEA;EACE,yBAAyB;EACzB,UAAU;AACZ;;AAEA;EACE,WAAW;EACX,2BAA2B;AAC7B;;AAEA,yBAAyB;;AAEzB;EACE,aAAa;EACb,sBAAsB;EACtB,aAAa;EACb,6BAA6B;EAC7B,WAAW;EACX,YAAY;AACd","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');\n\n*, *::before, *::after {\n    box-sizing: border-box;\n}\n\n* {\n    margin: 0;\n}\n  \nbody {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n}\n\nimg, picture, video, canvas, svg {\n    display: block;\n    max-width: 100%;\n}\n  \ninput, button, textarea, select {\n    font: inherit;\n}\n\np, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n}\n  \n#root, #__next {\n    isolation: isolate;\n}\n\nhtml {\n  --var-color-primary: #354034;\n  --var-color-secondary: #708C7C;\n}\n\nbody {\n  font-family: 'Poppins', sans-serif;\n  background-color: #faf8f2;\n  width: calc(100dvw - (100dvw - 100%));\n}\n\n.wave {\n  --mask: \n    radial-gradient(15px at 50% calc(100% - 20px), #000 99%, #0000 101%) calc(50% - 20px) 0/40px 100%, \n    radial-gradient(15px at 